<template>
    <div>
        <nav-bar></nav-bar>
        <br>
        <h1>Make a request</h1>
        <br>
        <b-form>
            <b-form-group style="position: relative"
                            id="dateBeginInputGrp"
                            label="Date begin : "
                            label-for="dateBegin">
                <date-picker v-model="form.dateBegin" :config="form.dateBegin.options"></date-picker>
            </b-form-group>
            <p>v-model: {{form.dateBegin}}</p>
            <b-form-group style="position: relative"
                            id="dateEndInputGrp"
                            label="Date end : "
                            label-for="dateEnd">
                <date-picker v-model="form.dateEnd" :config="form.dateEnd.options"></date-picker>
            </b-form-group>
            <p>v-model: {{form.dateEnd}}</p>
            <b-button type="submit" variant="primary">Edit informations</b-button>
    </b-form>
    </div>
</template>

<script>
import NavBar from '@/components/NavBar'
import datePicker from 'vue-bootstrap-datetimepicker';
import 'bootstrap/dist/css/bootstrap.css';
import 'pc-bootstrap4-datetimepicker/build/css/bootstrap-datetimepicker.css';

export default {
    name : "UserRequest",
    components: {
            NavBar, datePicker
    },
    data(){
        return {
            form: {
                dateBegin: new Date(),
                    options: {
                    format: 'DD/MM/YYYY',
                    useCurrent: false,
                },
                dateEnd: new Date(),
                    options: {
                    format: 'DD/MM/YYYY',
                    useCurrent: false,
                }  
            },
        }
    },
    mounted() {
            this.user = JSON.parse(localStorage.getItem('user'))
        },
    methods : {
        
    } 
}
</script>



